.script-issue {
  width: 100%;
  height: 100%;
 
  padding-bottom: 5px;
  .heading {
    width: 100%;
    height: 6%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    // border: 2px solid red;
    p {
      font-size: 1vw;
      font-weight: 500;
      color: #3f4d67;
      font-weight: 500;
    }

    button {
      width: 90px;
      padding: 4px 8px;
      background-color: #2f63c7;
      border: none;
      border-radius: 3px;
      cursor: pointer;
      p {
        color: white;
        font-size: 0.9vw;
        font-weight: 500;
      }
    }
  }

  .issue-box {
    width: 100%;
    height: 94%;
  
    .box-content {
      width: 100%;
      height: 100%;
      position: relative;
      overflow: hidden;
      box-shadow: #dfe5f9 0px 1px 3px 0px, #dfe5f9 0px 0px 0px 1px;
     
    }
  }

  .pop-up-issue {
    width: 100%;
    height: 100%;

    position: absolute;
    z-index: 10000;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.532);

    .pop-up {
      width: 450px;
      height: fit-content;
      background-color: white;
      border-radius: 3px;
      opacity: 0; /* Start hidden */
      transform: scale(0.8); /* Start slightly smaller */
      transition: opacity 0.5s ease, transform 0.5s ease; /* Fade and zoom effect */
      padding: 15px 10px;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      gap: 10px;
      flex-direction: column;
      box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;

      .summary-new {
        width: 100%;

        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        gap: 10px;
        height: fit-content;
        p {
          width: 100%;
          font-size: 1vw;
          color: #485567;
          font-weight: 500;
        }
        textarea {
          box-shadow: #dae1e9 0px 0px 1px 0px, #dae1e9 0px 0px 2px 0px;
          width: 100%;
          border-radius: 3px;
          padding: 5px;
          color: #485567;
          font-size: 0.8rem;
          border: 1px solid #dae1e9;
        }
        textarea:focus {
          border: 1px solid rgb(93, 114, 144);
          box-shadow: rgb(93, 114, 144) 0px 0px 2px 0px,
            rgb(93, 114, 144) 0px 0px 1px 0px;
          outline: none;
        }
      }

      .description-new {
        width: 100%;

        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        gap: 5px;
        p {
          width: 100%;
          font-size: 1vw;
          color: #485567;
          font-weight: 500;
        }
        textarea {
          box-shadow: #dae1e9 0px 0px 1px 0px, #dae1e9 0px 0px 2px 0px;
          width: 100%;
          border-radius: 3px;
          padding: 5px;
          color: #485567;
          font-size: 0.8rem;
          border: 1px solid #dae1e9;
        }
        textarea:focus {
          border: 1px solid rgb(93, 114, 144);
          box-shadow: rgb(93, 114, 144) 0px 0px 2px 0px,
            rgb(93, 114, 144) 0px 0px 1px 0px;
          outline: none;
        }
      }
      .result {
        width: 100%;

        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        gap: 5px;
        p {
          width: 100%;
          font-size: 1vw;
          color: #485567;
          font-weight: 500;
        }
        textarea {
          box-shadow: #dae1e9 0px 0px 1px 0px, #dae1e9 0px 0px 2px 0px;
          width: 100%;
          border-radius: 3px;
          padding: 5px;
          color: #485567;
          font-size: 0.8rem;
          border: 1px solid #dae1e9;
        }
        textarea:focus {
          border: 1px solid rgb(93, 114, 144);
          box-shadow: rgb(93, 114, 144) 0px 0px 2px 0px,
            rgb(93, 114, 144) 0px 0px 1px 0px;
          outline: none;
        }
      }

      .submit-new {
        width: 100%;
        text-align: right;
        button {
          padding: 5px 8px;
          font-size: 0.9vw;
          background-color: #2f63c7;
          color: white;
          border: none;
          border-radius: 3px;
          cursor: pointer;
        }
      }
    }
    .pop-up.show {
      opacity: 1; /* Fade in */
      transform: scale(1); /* Zoom to full size */
    }
  }
}
